<template>
    <div class="flex-col">
        <el-form :inline="true" class="query-box">
            <el-form-item label="上架时间">
                <el-date-picker
                        v-model="query.createDate"
                        type="datetimerange"
                        align="right"
                        start-placeholder="开始时间"
                        end-placeholder="结束时间"
                        format="yyyy-MM-dd HH:mm"
                        value-format="yyyy-MM-dd HH:mm"
                        :default-time="['6:00:00', '19:00:00']">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="商品类别">
                <el-select placeholder="请选择" v-model="query.type">
                    <el-option
                            v-for="item in goodsMap"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-input
                        placeholder="搜索商品名"
                        v-model="query.name"
                        @keyup.enter.native="search"
                        clearable>
                    <el-button slot="append" icon="el-icon-search" @click.native="search"></el-button>
                </el-input>
            </el-form-item>
        </el-form>

        <v-table v-on:pageChage="getData" :size="params.size" :page.sync="params.page"
                 :data="table.data" :total="table.total" :loading="table.loading">
            <el-table-column label="上架时间" align="center">
                <template slot-scope="scope">
                    <i class="el-icon-time"></i>
                    {{ scope.row.createDate }}
                </template>
            </el-table-column>
            <el-table-column label="商品名称" prop="name" align="center"></el-table-column>
            <el-table-column label="商品类别" align="center">
                <template slot-scope="scope">
                    {{translator(scope.row.type,goodsMap)}}
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" width="350">
                <template slot-scope="scope">
                    <el-button
                            plain
                            type="info"
                            icon="el-icon-view"
                            @click="handleView(scope.row)">查看
                    </el-button>
                    <el-button
                            plain
                            type="primary"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.row)">编辑
                    </el-button>
                    <el-button
                            plain
                            :type="!!scope.row.status ? 'warning' : 'success'"
                            :icon="!!scope.row.status  ? 'el-icon-remove-outline' : 'el-icon-circle-check-outline'"
                            @click="handleStatus(scope.row)">
                        {{!!scope.row.status ? '下架' : '上架'}}
                    </el-button>
                    <el-button
                            plain
                            type="danger"
                            icon="el-icon-delete"
                            @click="handleDelete(scope.row)">删除
                    </el-button>
                </template>
            </el-table-column>
        </v-table>

        <!--dialog 弹层-->
        <el-dialog v-el-drag-dialog :title="dialogTitle" center :visible.sync="dialog"
                   width="460px" @close="closeDialogCallBack">
            <el-form :model="formData" ref="form" status-icon :rules="rules" label-width="85px"
                     @keyup.enter.native="submitForm">

                <el-form-item label="商品名:" prop="name">
                    <el-input v-model="formData.name" placeholder="请输入商品名称" auto-complete="off" :disabled="isView"></el-input>
                </el-form-item>

                <el-form-item label="商户:" prop="business">
                    <el-input v-model="formData.business" placeholder="请输入商户名称" auto-complete="off" :disabled="isView"></el-input>
                </el-form-item>

                <el-form-item label="单价:" prop="price">
                    <el-input v-model.number="formData.price" placeholder="请输入商品单价" auto-complete="off" :disabled="isView"><template slot="append">元/斤</template></el-input>
                </el-form-item>

                <el-form-item label="备注:">
                    <el-input
                            v-model="formData.notes"
                            type="textarea"
                            :disabled="isView"
                            :autosize="{ minRows: 3, maxRows: 3}"
                            placeholder="备注">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer">
                <el-button @click="dialog = false">取 消</el-button>
                <el-button type="primary" v-show="!isView" @click="submitForm">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script type="text/ecmascript-6">
    import vTable from '@/components/vTable'
    import {apiGoodsList} from "@/api";
    import {logic,translator,dataMapping} from "@/utils";
    import {$SYS_ERR, goodsMap} from "@/config/dictionary";

    export default {
        name: 'goodsManage',
        data() {
            return {
                // 查询条件相关
                params: {
                    size: 20,
                    page: 1
                },
                // table列表相关
                table: {
                    data: [],
                    total: 0,
                    loading: false
                },
                query:{
                    createDate:'',
                    type:'',
                    name:''
                },
                goodsMap:goodsMap,
                translator:translator,
                // 弹层相关
                dialogTitle: '',
                dialog: false,
                isView: false,
                formData:{
                    name:'',
                    business:'',
                    price:'',
                    notes:''
                },
                rules: {
                    name: [
                        {required: true, message: '请输入商品名称', trigger: 'blur'},
                    ],
                    business: [
                        {required: true, message: '请输入商户名称', trigger: 'blur'},
                    ],
                    price: [
                        {required: true, message: '请输入商品单价', trigger: 'blur'},
                        { type: 'number', message: '单价必须为数字值',trigger: 'blur'}
                    ]
                },
                rowData:null
            }
        },
        created() {
            this.getData();
        },
        methods:{
            /**
             * 获取用户列表数据
             */
            getData(params = this.params) {
                this.table.loading = true;
                apiGoodsList(params)
                    .then(res => {
                        logic(res, result => {
                            this.table.data = result.data;
                            this.table.total = result.total;
                        });
                    })
                    .catch(err => {
                        this.$message({
                            type: 'error',
                            message: $SYS_ERR
                        });
                        throw Error(err);
                    })
                    .finally(() => {
                        this.table.loading = false;
                    })
            },
            /**
             *  筛选用户
             */
            search() {
                /**
                 *  这里分开写，防止点击分页时的请求参数受筛选条件的双向数据流的影响
                 *  唯有通过筛选条件进行的查询方可更新查询条件，点击翻页使用原始条件进行查询
                 */
                this.params.page = 1;
                this.getData(Object.assign(this.params, this.query));
            },
            /**
             * 打开弹层
             */
            openDialog() {
                this.dialog = true;
                dataMapping(this.formData,this.rowData);
            },
            /**
             * 关闭弹层回调
             */
            closeDialogCallBack() {
                this.$refs['form'].resetFields();
                setTimeout(()=>{
                    this.formData.notes = '';
                    this.isView = false;
                },200);
            },
            /**
             * 查看商品详情
             */
            handleView(row) {
                this.dialogTitle = '查看';
                this.isView = true;
                this.rowData = row;
                this.openDialog();
            },
            /**
             * 编辑商品详情
             */
            handleEdit(row) {
                this.dialogTitle = '编辑';
                this.rowData = row;
                this.openDialog();
            },
            /**
             * 删除商品
             */
            handleDelete(row) {
                this.$confirm('确定要删除该商品?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$notify({
                        title: '提示',
                        message: '删除成功',
                        type:'success',
                        duration: 800,
                        showClose: false
                    });
                });
            },
            // 提交表单
            submitForm() {

            },
            /**
             * 切换商品上架状态
             */
            handleStatus(row) {
                let message = '';
                !!row.status ? message = '下架' : message = '上架';
                this.$confirm('确定要'+message+'该商品？', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    row.status = !row.status;
                    this.$notify({
                        title: '提示',
                        message: message+'成功',
                        type:'success',
                        duration: 800,
                        showClose: false
                    });
                });
            }
        },
        components: {
            vTable
        }
    }
</script>

<style lang="scss" type="text/scss">

